import { _decorator, AudioClip, AudioSource, Component, Node, Toggle } from 'cc';
const { ccclass, property } = _decorator;

@ccclass('AudioToggle')
export class AudioToggle extends Component {
    static instance: AudioToggle;

    @property(AudioClip)
    public clips: AudioClip [] = [];

    @property(Toggle)
    public audioTog: Toggle = null;
    @property(Node)
    public turnOn: Node = null;
    @property(Node)
    public turnOff: Node = null;
    @property(AudioSource)
    public audioSrc: AudioSource = null;

    onAudioQueue(index: number){
        let clip: AudioClip = this.clips[index];
        this.audioSrc.playOneShot(clip);
    }

    audioTogFunc(){
        if(this.audioTog.isChecked){
            this.turnOn.active = true;
            this.turnOff.active = false;
            this.audioSrc.volume = 0.3;
        }else{
            this.turnOn.active = false;
            this.turnOff.active = true;
            this.audioSrc.volume = 0;
        }
    }
}


